@mixin publishers--portrait {
  @include portrait {
    flex-wrap: wrap;
    align-items: flex-start;
    overflow-x: hidden;

    .publishers__mockups {
      flex-basis: 100%;
      min-height: 50vh;
      margin: $lem * 2 0;

      .mockup--browser {
        .mockup__iframe {
          width: 80vw;

          .intrinsic-ratio-container {
            width: 115%;
          }
        }
      }

      .mockup--mobile {
        .mockup__iframe {
          width: 30vw;
        }
      }
    }
  }
}
